-direction: column;\n    align-items: center;\n    justify-content: center;\n    width: 100%;\n\n    &__top {\n        display: flex;\n        justify-content: space-between;\n        width: 100%;\n    }\n}\n\n.slick-initialized {\n    width: 100%;\n}\n\n.steps-coff.win {\n    border: 1px solid #00aa80;\n    color: #00aa80;\n}\n\n.steps-coff.lose {\n    border: 1px solid #fd7078;\n    color: #fd7078;\n}\n\n.steps-coff.active {\n    border: 1px solid #1f50fb;\n    color: #1e4ffb;\n}\n\n.cell.lose,\n.cell.win {\n    background-position: 50%;\n    background-repeat: no-repeat;\n    background-size: 98%;\n\n    &::after {\n        display: none;\n    }\n}\n\n.cell.lose {\n    background-image: url(\"~@/pages/Mines/assets/fail.svg\");\n}\n\n.cell {\n    height: 65px;\n    width: 65px;\n    position: relative;\n    cursor: pointer;\n    background-image: url(\"~@/pages/Mines/assets/cell.svg\");\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-position: center;\n\n    &::after {\n        content: \"\";\n        position: absolute;\n        width: 65px;\n        height: 65px;\n        top: 0;\n        left: 0;\n        z-index: 50;\n        background: url(\"~@/pages/Mines/assets/cell.svg\") no-repeat center/contain;\n    }\n}\n\n.cell.win {\n    background-image: url(\"~@/pages/Mines/assets/win.svg\");\n}\n\n.cell.opacity {\n    opacity: 0.4;\n}\n\n.form {\n    &__group {\n        margin-bottom: 0;\n    }\n\n    &__label {\n        margin-bottom: 8px;\n    }\n\n    .button_primary {\n        order: 3;\n        padding: 0;\n        font-size: 14px;\n        font-weight: 500;\n    }\n}\n\n@media (max-width: 1024px) {\n    .form {\n        &__input.changes {\n            padding-left: 12px;\n\n            input {\n                padding-left: 0;\n            }\n        }\n\n        &__group {\n            &:nth-child(1) {\n                order: 2;\n                margin-bottom: 8px;\n                margin-top: 0;\n\n                .form__buttons {\n                    display: none;\n                }\n            }\n\n            &:nth-child(2) {\n                order: 1;\n                margin-bottom: 0;\n            }\n\n            &:first-child {}\n        }\n\n        &__buttons {\n            &:nth-child(2) button {\n                height: 40px;\n            }\n        }\n    }\n\n    .page-mines {\n        &__cells {\n            padding: 0 28px;\n        }\n\n        &__steps {\n            gap: 0;\n        }\n\n        .slick-initialized {\n            width: calc(100% - 42px);\n            flex: none;\n        }\n\n        &__content {\n            background-color: var(--color-content);\n            padding: 16px;\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n            gap: 16px;\n\n            .steps-coff {}\n        }\n\n        &__form {\n            order: 3;\n            max-width: 100%;\n        }\n\n        &__steps {\n            width: 100%;\n            order: 1;\n            display: flex;\n            flex-direction: row;\n            justify-content: center;\n            height: 51px;\n\n            .steps-nav-button {\n                width: 51px;\n                flex: none;\n\n                &:first-child {\n                    transform: rotate(-90deg) translateY(15px);\n                }\n\n                &:last-child {\n                    transform: rotate(-90deg) translateY(-15px);\n                }\n            }\n        }\n\n        &__cells {\n            order: 2;\n            margin: 0;\n            width: 100%;\n        }\n    }\n\n    .cell {\n        aspect-ratio: 1/1;\n        width: 100%;\n        height: auto;\n\n        &::after {\n            aspect-ratio: 1/1;\n            width: 100%;\n            height: auto;\n        }\n    }\n}\n\n.win-t {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    background-color: #2E2A36;\n    border: 2px solid #8D24D5;\n    border-radius: 5px;\n    box-shadow: 2px 2px 2px 0 #0000004a;\n    color: #8D24D5;\n    padding: 5px 35px;\n    text-align: center;\n    user-select: none;\n    z-index: 55;\n}\n\n.win-t span {\n    display: block;\n    margin-bottom: 5px;\n}\n"],"sourceRoot":""}]);
